.header__element {
  position: fixed;
  width: 100%;
  z-index: 2;
}

.header__container:hover, .header__container.darkened {
  background-color: rgba(0, 0, 0, 0.8);
  transition: all 0.4s;
}

.header__box {
  height: 80px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-form-item {
  position: relative;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.search-form-item:before {
  color: #fff;
}

.search-form-item form.search {
  position: absolute;
  top: 49px;
  right: 0;
  width: 0;
  overflow: hidden;
  transition: all 0.4s;
}

.search-form-item form.search.active {
  width: 300px;
}

.search-form-item form.search input {
  display: block;
  width: calc(100% - 64px);
  padding: 0 10px;
  border: none;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.8);
}

.search-form-item form.search button {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  padding: 0 10px;
  background-color: #000;
  color: #fff;
}

.header__logo-box a {
  font-family: 'Montserrat-Bold';
  font-weight: 700;
  color: #fff;
  font-size: 30px;
}

.header__menu-box {
  position: relative;
}

.header__menu {
  position: relative;
  display: flex;
  align-items: center;
}

.menu__top-menu {
  display: flex;
}

.menu__top-menu li {
  margin: 0 52px 0 0;
}

.menu__top-menu li a {
  position: relative;
  color: #fff;
  font-size: 14px;
  text-transform: uppercase;
  transition: all 0.4s;
}

.menu__top-menu li a:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -12px;
  left: 0;
  width: 0;
  height: 3px;
  background-color: #fce38a;
  transition: all 0.4s;
}

.menu__top-menu li a:hover {
  text-decoration: none;
  color: #fce38a;
}

.menu__top-menu li a:hover:after {
  width: 100%;
}

.menu__top-menu li.shop-link {
  cursor: pointer;
}

.menu__top-menu li.shop-link a {
  display: none;
}

.menu__top-menu li.shop-link:before {
  color: #fff;
}

.menu-manage {
  display: none;
  position: absolute;
  width: 50px;
  height: 30px;
  top: 15px;
  right: 15px;
  color: #fff;
  transition: 0.4s ease-in-out;
  cursor: pointer;
  z-index: 1;
}

.menu-manage span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #fff;
  border-radius: 6px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

.main-menu-opening .menu-manage span {
  background-color: #999;
}

.menu-manage > span {
  z-index: 3;
}

.menu-manage span:nth-child(1) {
  top: 8px;
}

.menu-manage span:nth-child(2) {
  top: 15px;
}

.menu-manage span:nth-child(3) {
  top: 22px;
}

.top-menu-opened .menu-manage span:nth-child(1) {
  top: 18px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg);
}

.top-menu-opened .menu-manage span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

.top-menu-opened .menu-manage span:nth-child(3) {
  top: 18px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

.west-slider {
  position: relative;
  overflow: hidden;
}

.west-slider__item-box {
  min-height: 1000px;
  display: flex;
  overflow: hidden;
}

.west-slider__item-box img {
  min-height: 100%;
  object-fit: cover;
}

.west-slider__item {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.west-slider__item-img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-position: 50% 0;
}

.west-slider__item-background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, #fce38a, #f38181);
  opacity: 0.85;
  transition: all 0.4s;
}

.swiper-slide-active .west-slider__item-background {
  opacity: 0.85;
}

.west-slider__item-content-box {
  max-width: 800px;
  text-align: center;
  color: #fff;
}

.west-slider__item-content-box .west-slider__item-title {
  font-family: 'KaushanScript-Regular';
  font-size: 72px;
}

.west-slider__item-content-box .west-slider__item-subtitle {
  font-family: 'Montserrat Bold';
  font-size: 150px;
  line-height: 93%;
  margin-top: 65px;
  text-transform: uppercase;
}

.west-slider__nav-box {
  position: absolute;
  width: 100%;
  max-width: 1200px;
  left: calc(50% - 600px);
  bottom: 0;
  display: flex;
  z-index: 1;
}

.west-slider__nav-item {
  position: relative;
  flex: 0 0 25%;
  height: 74px;
  display: flex;
  align-items: center;
  color: #faf3dc;
  cursor: pointer;
  transition: all 0.4s;
}

.west-slider__nav-item:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 95%;
  height: 3px;
  background-color: #faf3dc;
  transition: all 0.4s;
}

.west-slider__nav-item:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 3px;
  background-color: #f38181;
  transition: all 0.4s;
}

.west-slider__nav-item .west-slider__nav-item-content {
  display: flex;
}

.west-slider__nav-item .west-slider__nav-item__number {
  font-family: 'Montserrat Bold';
  font-size: 24px;
  line-height: 1;
}

.west-slider__nav-item .west-slider__nav-item__content {
  display: flex;
  align-items: flex-end;
  line-height: 1;
  font-size: 18px;
  padding: 0 0 2px 5px;
  text-transform: uppercase;
}

.west-slider__nav-item:hover, .west-slider__nav-item.active {
  color: #fff;
}

.west-slider__nav-item:hover:before, .west-slider__nav-item.active:before {
  background-color: #fff;
}

.west-slider__nav-item.active:after {
  width: 20%;
  background-color: red;
}

.slider__item-button {
  display: inline-block;
  margin: 125px 0 0 0;
}

.slider__item-button:before {
  content: '';
  display: block;
  position: absolute;
  top: -72px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: #fff;
}

@media screen and (max-width: 1200px) {
  .header__box {
    padding: 0 20px;
  }
  .west-slider__item-box {
    min-height: 600px;
  }
  .west-slider__item-content-box .west-slider__item-title {
    font-size: 36px;
  }
  .west-slider__item-content-box .west-slider__item-subtitle {
    margin-top: 15px;
    font-size: 75px;
  }
  .slider__item-button {
    margin: 50px 0 0 0;
  }
  .slider__item-button:before {
    top: -32px;
  }
  .west-slider__nav-box {
    left: 0;
  }
}

@media screen and (max-width: 768px) {
  .menu-manage {
    display: block;
  }
  .header__menu.top-menu {
    display: block;
    position: absolute;
    width: 200px;
    padding: 0 15px 15px;
    top: 100%;
    right: -100%;
    background-color: rgba(0, 0, 0, 0.8);
    text-align: right;
    transition: all 0.4s;
  }
  .top-menu-opened .header__container {
    background-color: rgba(0, 0, 0, 0.8);
  }
  .top-menu-opened .header__menu.top-menu {
    right: 0;
  }
  .menu__top-menu {
    display: block;
    width: 100%;
    text-align: right;
  }
  .menu__top-menu li {
    margin: 0 0 15px 0;
  }
  .search-form-item {
    display: inline-block;
  }
  .west-slider__nav-item .west-slider__nav-item__number {
    font-size: 18px;
  }
  .west-slider__nav-item .west-slider__nav-item__content {
    font-size: 14px;
  }
  .search-form-item form.search {
    top: 36px;
    right: -15px;
  }
}

@media screen and (max-width: 480px) {
  .west-slider__item-content-box .west-slider__item-title {
    word-break: break-word;
  }
  .west-slider__item-content-box .west-slider__item-subtitle {
    word-break: break-word;
  }
  .west-slider__nav-item .west-slider__nav-item__number {
    font-size: 14px;
  }
  .west-slider__nav-item .west-slider__nav-item__content {
    font-size: 10px;
  }
}

